
import Xheader from '@/components/comother/rheader'
import Previewer from 'vux/src/components/previewer/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import ViewBox from 'vux/src/components/view-box/index.vue'

export default {
directives: {
TransferDom
},
components:{
'r-header':Xheader,
Previewer,
ViewBox
},
methods:{
show (index) {
this.$refs.previewer.show(index)
},
showPicInfo (index) {
this.$refs.previewer2.show(index)
},
spread(){
this.spr = true;
this.btnAn = !this.btnAn;
this.btnPack = !this.btnPack;
},
folding(){
this.spr = !this.spr;
this.btnAn = !this.btnAn;
this.btnPack = !this.btnPack;
},
noSpread(){
this.noSpr = true;
this.nobtnAn = !this.nobtnAn;
this.nobtnPack = !this.nobtnPack;
},
noFolding(){
this.noSpr = !this.noSpr;
this.nobtnAn = !this.nobtnAn;
this.nobtnPack = !this.nobtnPack;
},
logIndexChange (arg) {
console.log(arg)
},
getData(){
this.$http.post('active/queryById?activeId='+this.$route.params.activeId
).then(res =>{
this.activeData = res.data;
this.contents.title = res.data.activeTypeName+'详情';
this.activeNames = this.activeData.activeTypeName;
this.peopleNum = this.activeData.notParticipate.length + this.activeData.participate.length;
this.participants = this.activeData.participate.length;
this.Noparticipants = this.activeData.notParticipate.length;
if(this.participants >= 4){
this.btnAn = true
}
if(this.Noparticipants >= 4){
this.nobtnAn = true
}
if(this.activeData.picIds)
{
var picIdArr = this.activeData.picIds.split(",")
picIdArr.forEach(element => {
if(element)
{
var obj = {};
obj.msrc = 'http://www.dlbdata.cn/dangjian/picture/showThumbnail?pictureId='+element;
obj.src = 'http://www.dlbdata.cn/dangjian/picture/show?pictureId='+element;
this.picList.push(obj);
}
});
}


}).catch(err =>{
console.log(err)
})
},
getPic(){
this.$http.post('active/getActivePictures?activeId='+this.$route.params.activeId

).then(res =>{
this.picInfo= res.data;
for(var d=0 ;d<this.picInfo.length;d++){
var obj = {};
obj.msrc = 'http://www.dlbdata.cn/dangjian/picture/showThumbnail?pictureId='+this.picInfo[d].pictureId;



obj.src = 'http://www.dlbdata.cn/dangjian/picture/show?pictureId='+this.picInfo[d].pictureId;



this.list.push(obj);
}
}).catch(err =>{
console.log(err)
})
},
dataPick(s){
Date.prototype.toLocaleString = function(){
return this.getFullYear() +'年'+ (this.getMonth()+1)+'月'+this.getDate()+'日'
}
return new Date(s).toLocaleString();
}
},
mounted(){
this.getData();
this.getPic();
},
filters: {
formatDuring: function (value) {
if(value == "" || value == null || value == undefined){
var value="无";
return value

}else {
Date.prototype.toLocaleString = function(){
let months = this.getMonth()+1;
if(months < 10){
months = '0' + months;
}
let dates = this.getDate();
if(dates < 10){
dates = '0' + dates;
}
let hours = this.getHours();
if(hours < 10){
hours = '0' + hours;
}
let minutes = this.getMinutes();
if(minutes < 10){
minutes = '0' + minutes;
}
return this.getFullYear() +'.'+months+'.'+dates+' '+hours+':'+minutes
}
return new Date(value).toLocaleString();
}}
},
data(){
return {
num:0,
activeData:{},
picInfo:[],
list: [],
picList : [],
spr:false,
noSpr:false,
nobtnPack:false,
nobtnAn:false,
btnPack:false,
btnAn:false,
peopleNum:null,
participants:null,
Noparticipants:null,
activeNames:'',
contents:{rights:'',title:'bbb'},
roleId:this.$store.getters.user.roleid,
options: {
getThumbBoundsFn (index) {

let thumbnail = document.querySelectorAll('.previewer-demo-img')[index]

let pageYScroll = window.pageYOffset || document.documentElement.scrollTop


let rect = thumbnail.getBoundingClientRect()

return {x: rect.left, y: rect.top + pageYScroll, w: rect.width}


}
}
}
}
}



